export async function loadTheme() {
    const theme = localStorage.getItem('atomathTheme') || 'dark'
    return import(`./theme.${theme}.js`); // provided that theme.light.js & theme.dark.js are in the same directory
}
export function toggleTheme() {
    const prev = localStorage.getItem('atomathTheme') || 'dark'
    const next = prev == 'dark' ? 'light' : 'dark'
    localStorage.setItem('atomathTheme', next)
    window.location.reload();
}